import React from "react";
import styled from "styled-components";

const TitleText = styled.div`
  font-family: var(--normal-font);
  font-size: calc(0vw + 1.5rem);
  color: #ffffff;
`;

const ProjectCard = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50%;
  margin: 1vw;
  background-color: none;
  border-radius: 20px;
  box-shadow: 0px 0px 0px 3px var(--color-home-border);
  padding: 5px 10px 10px 10px;
  transition: all 0.2s linear;
  &:hover {
    box-shadow: 0px 0px 0px 10px var(--color-home-border);
  }
`;

const SiteButton = styled.div`
  width: 40px;
  height: 20px;
  background-color: #3182ce;
  border-radius: 5px;
  padding: 6px;
  margin: 0px 0px 0px 10px;
  text-align: center;
`;

const Text = styled.span`
  color: #ffffff;
`;

const URLLink = styled.a`
  text-decoration: none;
  color: #ffffff;
`;

export default function Project({ description, siteLink, codeLink }) {
  return (
    <ProjectCard>
      <TitleText>{description}</TitleText>
      {siteLink === "" ? null : (
        <SiteButton>
          <Text>
            <URLLink href={siteLink}>Site</URLLink>
          </Text>
        </SiteButton>
      )}
      {codeLink === "" ? null : (
        <SiteButton>
          <Text>
            <URLLink href={codeLink}>Code</URLLink>
          </Text>
        </SiteButton>
      )}
    </ProjectCard>
  );
}
